@import './../../../styles/variables';

.c-Plp__c-ProductContainer__c-ProductTile {
    // box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    max-width: 300px;
    // margin-bottom: 2rem;
    padding: 10px 15px;
    border: 1px solid $neutral-17;

    .c-Plp__c-ProductContainer__c-ProductTile__image {
        margin-bottom: 0.7rem;
    }

    .c-Plp__c-ProductContainer__c-ProductTile__name {
        font-size: $small-font-size;
    }

    .c-Plp__c-ProductContainer__c-ProductTile__price__container {
        line-height: $smaller-font-size;
        margin-bottom: 0.9rem;
        display: flex;
        .c-Plp__c-ProductContainer__c-ProductTile__price {
            font-size: $smaller-font-size;
            font-weight: $bold-font-weight;
            padding-right: 0.5rem;
        }
        
        .c-Plp__c-ProductContainer__c-ProductTile__price--strikethrough {
            color: $neutral-40;
            font-weight: $bold-font-weight;
            font-size: $tiny-font-size;
            text-decoration: line-through;
        }
        
        .c-Plp__c-ProductContainer__c-ProductTile__discount {
            color: #4aa219;
            font-weight: $bold-font-weight;
            font-size: $tiny-font-size;
            margin-left: auto;
        }
    }

    button {
        display: block;
        margin: 0 auto;
        border-radius: 20px;
        padding: 7px;
        background-color: $add-to-cart-button-background-color;
        cursor: pointer;
        font-size: $smaller-font-size;
        font-weight: $bold-font-weight;
    }
    
    button:hover {
        opacity: 0.7;
    }

    @media only screen and (min-width: 768px) {
        
    }
}
